body {
  margin: 0;
  padding: 0;
  font-family: Arial, Helvetica, sans-serif;
  background-color: #fff;
  background-image:
    linear-gradient(90deg, transparent 79px, #abced4 79px, #abced4 81px, transparent 81px),
    linear-gradient(#eee .1em, transparent .1em);
  background-size: 100% 1.2em;
}
.form {
  padding: 22px 0;
  font-size: 20px;
  color: #313957;
  border: 3px solid #5c7cfa;
}

.form input {
  margin: 10px;
  padding: 5px 15px;
  border: 2px solid #576499;
  font-size: 18px;
  outline: none;
}

.form input[type="text"] {
  padding: 10px 8px;
}

.form input[type="text"]:focus {
  color: #313957;
}

.form input[type="text"]:focus {
  color: #757a8b;
}

.form button {
  padding: 10px 30px;
  margin: 20px 0 0;
  border-style: none;
  text-transform: uppercase;
  font-size: 20px;
  color: white;
  background-color: #5c7cfa;
  outline: none;
  cursor: pointer;
}

.form button:hover {
  background-color: #4263eb;
}

.form .important {
  position: relative;
}

/* custom checkbox */

.form input[type="checkbox"] {
  opacity: 0;
}

.form .custom-checkbox {
  position: absolute;
  top: 0;
  left: 0;
  height: 25px;
  width: 25px;
  background: #576499;
  border: 2px solid #576499;
  cursor: pointer;
}

.form .custom-checkbox::after {
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  height: 0;
  width: 0;
  border-width: 0 2px 2px 0;
  -webkit-transform: rotate(0deg) scale(0);
          transform: rotate(0deg) scale(0);
}

.form input[type="checkbox"]:checked~.custom-checkbox::after {
  top: 5px;
  left: 7px;
  height: 7px;
  width: 10px;
  border: solid white;
  border-width: 0 2px 2px 0;
  -webkit-transform: rotate(45deg) scale(1);
          transform: rotate(45deg) scale(1);
}
h2,
h3 {
    margin: 25px 0;
    text-transform: uppercase;
    letter-spacing: 1px;
}

.active,
.done {
    color: #313957;
    padding-bottom: 15px;
}

.active {
    border-bottom: 3px solid #5c7cfa;
}

.active button:nth-of-type(1) {
    margin-right: 5px;
    padding: 10px 15px;
    border-style: none;
    text-transform: uppercase;
    font-size: 16px;
    color: white;
    background-color: #5c7cfa;
    outline: none;
    cursor: pointer;
}

.active button:nth-of-type(1):hover {
    background-color: #4263eb;
}

.active button:nth-of-type(2),
.done button {
    padding: 10px 15px;
    border-style: none;
    background-color: #966174;
    color: white;
    font-size: 16px;
    outline: none;
    cursor: pointer;
}

.done button {
    margin-left: 5px;
}

.active button:nth-of-type(2):hover,
.done button:hover {
    background-color: #92425f;
}
body {
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.App {
  box-sizing: border-box;
  width: 50%;
  min-height: 100vh;
  margin: 0 auto;
  text-align: center;
  padding: 15px 15px 0;
  background-color: rgba(151, 169, 244, 0.5);
}

h1 {
  color: #313957;
}

/* aside text */

.lettering {
  position: fixed;
  top: 30px;
  right: 35px;
  font-family: 'Indie Flower', cursive;
  font-size: 42px;
  color: #92425f;
  -webkit-transform: rotate(20deg);
          transform: rotate(20deg);
}
